.images {
  line-height: 0;
  column-count: 5;
  column-gap: 1px;
}
.images img {
  width: 100% !important;
  height: auto !important;
  margin: 1px !important;
}
@media (max-width: 1200px) {
  .images {
    column-count: 4;
  }
}
@media (max-width: 1000px) {
  .images {
    column-count: 3;
  }
}
@media (max-width: 800px) {
  .images {
    column-count: 2;
  }
}
@media (max-width: 400px) {
  .images {
    column-count: 1;
  }
}
